<template>
    <div id="app" justify>
        <v-img src="../assets/image/back2.png">
            <v-row>
                <v-col col="12" sm="5">
                </v-col>
                <v-col col="12" sm="2">
                    <v-img src="../assets/image/howto.png"></v-img>
                </v-col>

            </v-row>

            <v-row class="content">
                <v-col cols="12" sm="1">
                </v-col>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog" class="card">
                        <h1 class="step-title">採菁 <v-btn icon class="xx" color="#ffffff" @click="dialog = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step1_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step1-1.png"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">
                            <v-row>
                                <v-col cols="12" sm="6">
                                    <video src="../assets/image/step1.mp4" width="550" height="440" controls></video>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>採菁方式可分為人工與機器兩種。人工採菁是用食指與姆指將茶葉梗摘斷，機器採收則是使用專門的採菁機採茶。文山包種茶通常採一心二葉或是一心三葉，也就是包含頂芽及2~3片已舒張之葉片。早上11點至下午3點採摘品質最佳，高級的文山包種茶通常遇雨不採，否則香氣難以發揮。</h4>
                                </v-col>
                            </v-row>

                        </v-sheet>
                    </v-dialog>
                </v-col>
                <v-icon large>mdi-arrow-right</v-icon>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog1" class="card">
                        <h1 class="step-title">萎凋 <v-btn icon class="xx" color="#ffffff" @click="dialog1 = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step2_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step2-1.jpg"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">

                            <v-row>
                                <v-col cols="12" sm="6">
                                    <video src="../assets/image/step2.mp4" width="550" height="440" controls></video>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>日光萎凋:茶菁須於日光下攤曬，或利用熱風使茶菁水分適度蒸散，減少細胞水分含量。萎凋過程中每一階段水份變化，是提高茶葉品質的重要技術，若芽葉出現積水現象，會導致滋味菁澀、香氣不揚、水色偏黃、色澤暗綠；若芽葉消水，則會導致滋味淡薄、 香氣不揚、水色淡綠、色澤黃綠之品質。
                                        室內萎凋: 經過一段適當時間日光萎凋後，須將茶菁移至室內靜置萎凋。適宜之萎凋條件為室溫22~25℃及相對溼度70~80%之製茶環境，控制芽葉水分蒸發使茶葉色、香、味能完整呈現。
                                    </h4>
                                </v-col>
                            </v-row>
                        </v-sheet>
                    </v-dialog>
                </v-col>
                <v-icon large>mdi-arrow-right</v-icon>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog2" class="card">
                        <h1 class="step-title">發酵 <v-btn icon class="xx" color="#ffffff" @click="dialog2 = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step3_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step3-1.png"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">
                            <v-row>
                                <v-col cols="12" sm="6">
                                    <v-img src="../assets/image/b.png"></v-img>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>為了讓水份從葉緣水孔和葉底氣孔蒸發掉。</h4>
                                </v-col>
                            </v-row>
                        </v-sheet>
                    </v-dialog>
                </v-col>

            </v-row>
            <v-row class="content">
                <v-col cols="12" sm="1">
                </v-col>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog3" class="card">
                        <h1 class="step-title">浪菁 <v-btn icon class="xx" color="#ffffff" @click="dialog3 = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step4_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step4-1.jpg"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">
                            <v-row>
                                <v-col cols="12" sm="6">
                                    <video src="../assets/image/step4.mp4" width="550" height="440" controls></video>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>用雙手或機器不斷翻動茶菁，藉由翻動來調節茶葉醱酵的程度。</h4>
                                </v-col>
                            </v-row>
                        </v-sheet>
                    </v-dialog>
                </v-col>
                <v-icon large>mdi-arrow-right</v-icon>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog4" class="card">
                        <h1 class="step-title">炒菁 <v-btn icon class="xx" color="#ffffff" @click="dialog4 = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step5_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step5-1.jpg"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">
                            <v-row>
                                <v-col cols="12" sm="6">
                                    <video src="../assets/image/step5.mp4" width="550" height="440" controls></video>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>茶菁萎凋至適當程度即以高溫炒菁破壞茶葉中酵素活性，停止繼續醱酵，新鮮葉葉也因水分的蒸散而便於接下來進行揉捻。</h4>
                                </v-col>
                            </v-row>
                        </v-sheet>
                    </v-dialog>
                </v-col>
                <v-icon large>mdi-arrow-right</v-icon>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog5" class="card">
                        <h1 class="step-title">揉捻 <v-btn icon class="xx" color="#ffffff" @click="dialog5 = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step6_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step6-1.jpg"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">
                            <v-row>
                                <v-col cols="12" sm="6">
                                    <video src="../assets/image/step6.mp4" width="550" height="440" controls></video>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>將炒菁後的茶葉置入揉捻機內，使其滾動形成條索狀，由於受到揉壓，因此有部分汁液被擠出而黏附於表面，如此在沖泡時便可很容易地溶解於茶湯之中，而不同的茶種其揉捻程度也不一樣</h4>
                                </v-col>
                            </v-row>
                        </v-sheet>
                    </v-dialog>
                </v-col>

            </v-row>
            <v-row class="content">
                <v-col cols="12" sm="1">
                </v-col>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog6" class="card">
                        <h1 class="step-title">乾燥 <v-btn icon class="xx" color="#ffffff" @click="dialog6 = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step7_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step7-1.jpg"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">
                            <v-row>
                                <v-col cols="12" sm="6">
                                    <video src="../assets/image/step7.mp4" width="550" height="440" controls></video>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>利用高溫停止炒菁時殘留的酵素活性，使茶葉不再醱酵，固定茶葉品質。以熱風烘乾揉捻後之茶葉，使其含水量低於4%，利於貯藏運銷。通常為了使內外乾燥一致，常採用二次乾燥法，第一次先使其達到七、八成乾燥，然後取出回潮，再進行第二次的乾燥。此步驟完成的茶葉稱為「毛茶」或「初製茶」。</h4>
                                </v-col>
                            </v-row>
                        </v-sheet>
                    </v-dialog>
                </v-col>
                <v-icon large>mdi-arrow-right</v-icon>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog7" class="card">
                        <h1 class="step-title">培火 <v-btn icon class="xx" color="#ffffff" @click="dialog7 = false">
                                <v-icon>mdi-close</v-icon>
                            </v-btn>
                        </h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step8_0.png"></v-img>
                                <v-img v-on="on" class="img-top" src="../assets/image/step8-1.jpg"></v-img>
                            </figure>
                        </template>
                        <v-sheet color="#7B7663">
                            <v-row>
                                <v-col cols="12" sm="6">
                                    <video src="../assets/image/step8.mp4" width="550" height="440" controls></video>
                                </v-col>
                                <v-col class="step" cols="12" sm="6">
                                    <h4>茶葉經過乾燥步驟後，以影片中的箱型乾燥機進行最後焙火步驟。目的在於降低水份以利茶葉品質的保存，並將文山包種茶香氣藉由焙火程序完全釋放出來。</h4>
                                </v-col>
                            </v-row>
                        </v-sheet>
                    </v-dialog>
                </v-col>
                <v-icon large>mdi-arrow-right</v-icon>
                <v-col cols="12" sm="3">
                    <v-dialog v-model="dialog8" class="card">
                        <h1 class="step-title">採菁</h1>
                        <template v-slot:activator="{ on }">
                            <figure class="card">
                                <v-img src="../assets/image/step9_0.png"></v-img>
                                <v-img class="img-top" src="../assets/image/step9-1.png"></v-img>
                            </figure>
                        </template>

                    </v-dialog>
                </v-col>

            </v-row>
        </v-img>
    </div>
</template>
<script > 
export default {
    el: '#app',
    name: 'Home',
    component: {

    },
    data() {
        return {
            dialog: false,
            dialog1: false,
            dialog2: false,
            dialog3: false,
            dialog4: false,
            dialog5: false,
            dialog6: false,
            dialog7: false,
            dialog8: false,
        }
    },

}
</script>
<style type="text/css">
  .content{

}
.card {
        width: 250px;
        position: relative;
        display: inline-block;
    }
    .card .img-top {
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 99;
    }
    .card:hover .img-top {
        display: inline;
    }
.step{
    font-family: 'setofont';
    font-weight: lighter;
    font-size: 28px;
    color: #ffffff;
    padding: 20px;
    margin: 100px 0px 0px 0px;

}    
 
.step-title{
        align-items: center;
    text-align: center;
    font-size: 35px;
    color: #FFFFFF;
    background-color: #393420;

} 
.xx{
    position: absolute;
    right: 28px;
}
</style>