import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../components/home.vue'
import Tea from '../components/tea.vue'
import Step from '../components/step.vue'
import Knowtea from '../components/knowtea.vue'
import Story from '../components/story.vue'
import About from '../components/about.vue'
import Nature from '../components/nature.vue'
import history from '../components/history.vue'




Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home,
  },
    {
    path: '/tea',
    name: 'tea',
    component:Tea,
  },
    {
    path: '/step',
    name: 'step',
    component:Step,
  },
  {
    path: '/knowtea',
    name: 'knowtea',
    component:Knowtea,
  },
    {
    path: '/story',
    name: 'story',
    component:Story,
  },
     {
    path: '/nature',
    name: 'nature',
    component:Nature,
  },
  {
    path: '/about',
    name: 'about',
    component: About,
  },
    {
    path: '/history',
    name: 'history',
    component: history,
  },

]


const router = new VueRouter({
  mode:'history',
  routes

})

export default router
